<template>
  <div>
    <keep-alive>
      <router-view v-if='$route.meta.keepAlive'></router-view>
    </keep-alive>
    <router-view v-if='!$route.meta.keepAlive'></router-view>

  </div>
</template>

<script>
export default {
  name: 'news-index',
  methods: {
    showNewsList () {
      this.$router.push({ name: 'newsList' });
    },
    showNewsDetail (newsID) {
      this.$router.push({
        name: 'newsDetail',
        params: {
          id: newsID,
        },
      })
    },
    showNewsNew () {
      this.$router.push({
        name: 'newsNew'
      })
    },
  },
  mounted () {
    this.$bus.$on('showNewsList', this.showNewsList);
    this.$bus.$on('showNewsDetail', this.showNewsDetail);
    this.$bus.$on('showNewsNew', this.showNewsNew);
  },
  beforeDestroy () {
    this.$bus.$off('showNewsList');
    this.$bus.$off('showNewsDetail');
    this.$bus.$off('showNewsNew');
  }
}
</script>

